import { Alert } from "antd";
import TextLoop from "react-text-loop";

const ConvertTips = () => {
  return (
    <Alert
      style={{
        width: "90%",
        margin: "0 auto",
        height: "auto",
      }}
      banner
      message={
        <TextLoop mask noWrap={false}>
          <div className="">
                     1200   4 
            
          </div>
          <div className="">
                   300  .
          </div>
          <div className="">
                   2  .
          </div>
          <div className="">
                   .     
            
          </div>
        </TextLoop>
      }
    />
  );
};

export default ConvertTips;
