import { Alert } from "antd";
import TextLoop from "react-text-loop";

const TransferTips = () => {
  return (
    <div className="" style={{ height: 100, paddingTop: "15px" }}>
      <Alert
        style={{
          width: "90%",
          margin: "0 auto",
          height: "auto",
        }}
        banner
        message={
          <TextLoop mask noWrap={false}>
            <div className="">   5%      </div>
            <div className="">
                       1200   
            </div>
            <div className="">     300  .</div>
            <div className="">
                     .    
               
            </div>
          </TextLoop>
        }
      />
    </div>
  );
};

export default TransferTips;
